import req from './https.js';
export const apiGetState = parmas => req('get', '/api/Pork', parmas) //取得模組的診斷運行狀態、包含是否在診斷、診斷模型列表
export const apiPostSalePrice = params => req('post', '/api/Pork/SaleData', params) //進行模型訓練 (帶參數)
export const apiGetTrainProcess = params => req('get', '/Diagnose/TrainProcess', params); //取得訓練的狀況