<template>
  <div>
    <el-image
      style="width: 100px; height: 100px"
      :src="imgUrl"
      :fit="fit"
    ></el-image>
    <h1 v-if="show_text">{{ DyText }}</h1>
    <el-button type="button" @click="btnClick"> 今天吃啥 </el-button>
  </div>
</template>

<script>
import { apiGetState, apiPostSalePrice } from "../apis/Test.js";
export default {
  data() {
    return {
      show_text: false,
      SaleInfo: {
        Date: "20201222",
        Number: 69,
        AveragePrice: 69.99,
      },
    };
  },
  methods: {
    async btnClick() {
      this.show_text = !this.show_text;
      let res_DXStatus = await apiGetState(this.ModuleIP);
      console.log(res_DXStatus);
      this.SaleInfo.Number += 1;
      let res_post = await apiPostSalePrice(this.SaleInfo);
      console.log(res_post);
    },
  },
  props: {
    DyText: String,
    imgUrl: String,
  },
};
</script>

