import axios from 'axios';
import store from '../store/index.js';
import {
    show_error_msg
} from './utils.js';

const errorHandler = (status, msg) => {
    show_error_msg('WEB API REQUEST ERROR(' + status + ')');

    switch (status) {
        case 400:
            console.log(400)
            break;
        case 401:
            console.log(401)
            break;
        case 403:
            console.log(403)
            break;
        case 404:
            console.log(404)
            break;
        default:
            console.log(msg);
    }
}

var _baseURL = "localhost:8080"

var instance = axios.create({
    baseURL: store.state.Server_URL
})



