function ShowNetworkErrorMsg() {
    show_error_msg('連線出了問題>Server url:' + _baseURL, 1000)
}

export default function (method, url, data = null) {

    //if (_baseURL == null) {
    //   console.log('test null')
    //Initialize();
    //  _baseURL = store.state.Server_URL
    //   console.log(_baseURL)
    //}

    method = method.toLowerCase();
    if (method == 'post') {
        return instance.post(url, data).catch(error => {
            console.log('web api GET fail>' + error)
            ShowNetworkErrorMsg();
        })
    } else if (method == 'get') {
        return instance.get(url, {
            params: data
        }).catch(error => {
            console.log('web api GET fail>' + error)
            ShowNetworkErrorMsg();
        })
    } else if (method == 'delete') {
        return instance.delete(url, {
            params: data
        }).catch(error => {
            console.log('web api GET fail>' + error)
            ShowNetworkErrorMsg();
        })
    } else if (method == 'put') {
        return instance.put(url, data).catch(error => {
            console.log('web api GET fail>' + error)
            ShowNetworkErrorMsg();
        })
    } else {
        return false;
    }

}