<template>
  <div class="home">
    <div class="banner">
      <div>
        <img :src="'/assets/idms_banner.png'" />
      </div>
      <el-divider></el-divider>
      <div class="user_info">
        <el-button type="primary" size="small" @click="ShowVersion"
          >DEMO KIT 3</el-button
        >
        <el-tag :type="this.LoginState.IsLogining? 'success': 'info'">{{ this.LoginState.UserName }}</el-tag>
        <el-button type="danger" v-if="LoginState.IsLogining" size="small"
          @click="Logout" >登 出</el-button
        >
        <el-button
          type="info"
          icon="el-icon-user"
          circle
          @click="LogindialogFormVisible = true"
        ></el-button>
      </div>
    </div>
    <h1>GPM IDMS 主頁^^</h1>
    <!-- 主要顯示區塊 -->
    <div class="Mains_show">
      <el-collapse class="collpase_inner" >
        <el-collapse-item title="spot" name="1">
          <ModuleStateView EqName="ROBOT" UnitName="1st Axis"></ModuleStateView>
        </el-collapse-item>
        <el-collapse-item title="spot" name="2">
          <ModuleStateView EqName="ROBOT" UnitName="1st Axis"></ModuleStateView>
        </el-collapse-item>
        <el-collapse-item title="spot" name="3">
          <ModuleStateView EqName="ROBOT" UnitName="1st Axis"></ModuleStateView>
        </el-collapse-item>
      </el-collapse>
    </div>

    <!-- login 畫面-->
    <el-dialog title="LOGIN" :visible.sync="LogindialogFormVisible" width="540">
      <el-button type="info" icon="el-icon-user" circle></el-button>
      <el-divider></el-divider>
      <el-form :model="form">
        <el-form-item label="USER NAME" :label-width="formLabelWidth">
          <el-input
            :disabled="LoginState.IsLogining"
            v-model="form.name"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item label="PASSWORD" :label-width="formLabelWidth">
          <el-input
            :disabled="LoginState.IsLogining"
            show-password
            v-model="form.password"
            autocomplete="off"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="LogindialogFormVisible = false">取 消</el-button>
        <el-button
          :type="LoginState.IsLogining ? 'danger' : 'primary'"
          @click="LoginDataPost"
          >{{ LoginState.IsLogining ? "登 出" : "確 定" }}</el-button
        >
      </div>
    </el-dialog>
    <!---->
  </div>
</template>

<script>
import { show_success_msg, show_error_msg } from "../../apis/utils.js";
import ModuleStateView from "../../components/DemoKit/ModuleStateView.vue";
// @ is an alias to /src
export default {
  name: "Home",
  data() {
    return {
      activeNames: ["1", "2", "3"],
      LogindialogFormVisible: false,
      LoginState: {
        UserName: "VISITOR",
        IsLogining: false,
      },

      form: {
        name: "",
        password: "",
        ComfirmBtnText: "確 定",
      },
      formLabelWidth: "120px",
    };
  },
  components: { ModuleStateView },
  methods: {
    ShowVersion() {
      this.$alert("v3.0.1", "Version", {
        confirmButtonText: "OK",
      });
    },
    LoginDataPost() {

       if(this.LoginState.IsLogining==true)
       {
         this.Logout();
         this.LogindialogFormVisible=false;
         return;
       }

      console.log("送出登入請求到後端");
      var isLoginSuccess = true;
      if (isLoginSuccess) {
        this.LogindialogFormVisible = false;
        console.log(this.form);
        this.LoginState.UserName = this.form.name;
        this.LoginState.IsLogining = true;
        show_success_msg("Hi," + this.LoginState.UserName);
      } else {
        this.LoginState.IsLogining = false;
        this.LogindialogFormVisible = true;
        show_error_msg("登入失敗");
      }
    },
    Logout(){
      console.log('登出');
      this.form.password='';
      this.LoginState.UserName="VISITOR",
      this.LoginState.IsLogining=false;
      show_success_msg("登出成功");
      
    },
    Collapse_handleChange() {},
  },
};
</script>
